<script>
import Container from '@/components/Container.vue'
import InstaPosts from "@/data/InstaPosts";
import axios from 'axios'
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'App',
  components: {
    Container,
  },
  data() {
    return {
      InstaPosts: [],
      moreReqCnt: 0,  // 다음 포스트 요청 횟수
      currentTab: 0,  // 현재 탭의 위친

      // 현재페이지 상태.
      // 동적인 UI는
      // 1. ui 만들기
      // 2.ui 상태값을 데이터로 만들기
      // 3. 데이터 상태에 따라 보여질 html 정의해놓기.
      // 4. 데이터 상태를 사용자가 변경 가능하도록 기능 개발
      // currentPage: 'POST',
      currentPage: 'WRITE',
      imgURL: '',
      myWriting: '',
      selectedFilter: '',
      counter: 0,
    }
  },
  computed: {
    now2: function() {
      return new Date()
    },
    // getName() {
    //   return this.$store.state.name
    // },
    // getAge() {
    //   return this.$store.state.age
    // },
    // vuex state 한번에 꺼내기: ...mapState
    ...mapState(['name', 'age', 'likes']),
    ...mapState({getName: 'name', getAge: 'age'}),
  },
  watch: {
    'selectedFilter'() {
      console.log('watch: selectedFilter >>', this.selectedFilter)
    },
  },
  created() {
    this.InstaPosts = InstaPosts
  },
  mounted() {
    this.emitter.on('filterName', (filter) => {
      this.selectedFilter = filter
    })
  },
  methods: {
    // vuex에서 mutatition 메서드들 갖다 쓰기
    ...mapMutations(['changeName', 'increaseAge']),
    now: function() {
      return new Date()
    },
    getMorePosts: function() {
      if (this.moreReqCnt < 2) {
        axios.get(`https://codingapple1.github.io/vue/more${this.moreReqCnt}.json`)
        .then((result) => {
          if (result) {
            console.log('result.data >>', result.data)
            this.InstaPosts.push(result.data)
            this.moreReqCnt++
          }
        })
      }
    },
    uploadPost: function(e) {
      const uploadFile = e.target.files
      console.log('uploadFile[0] >>', uploadFile[0])
      this.imgURL = URL.createObjectURL(uploadFile[0])
      // console.log('imgURL >>', imgURL)
      this.currentPage = 'FILTER'
    },
    publishPost: function() {
      const newPost = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imgURL,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.myWriting,
        filter: this.selectedFilter,
      }
      console.log('newPost >>', newPost)
      this.InstaPosts.unshift(newPost)
      this.currentPage='POST'
    },
    setMyWriting: function(write) {
      this.myWriting = write
    },
  },
}
</script>

