<template>
  <div
    :class="`filter-item ${filter}`"
    :style="{backgroundImage: `url(${imgURL})`}"
    @click="fireEvent"
  >
     <slot></slot>
    <!-- <slot>{{ filter }}</slot>-->
  </div>
</template>

<script>
export default {
  name: "FilterBox",
  props: {
    imgURL: {
      type: String,
      default: null,
    },
    filter: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      msg: 'hello',
    }
  },
  methods: {
    fireEvent: function() {
      this.emitter.emit('filterName', this.filter)
    },
  },
}
</script>

<style scoped>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>