<template>
  <div style="padding : 10px">
    <h4>팔로워</h4>
    <input placeholder="?" @input="searchFollower($event.target.value)"/>
    <div
      class="post-header"
      v-for="(follower, index) in followers"
      :key="index"
    >
      <div class="profile" :style="`background-image: url(${follower.image})`"></div>
      <span class="profile-name">{{ follower.name }}</span>
    </div>
  </div>
</template>

<script>
import { ref, toRefs, reactive, onMounted, watch, computed } from 'vue';
import axios from 'axios';
import { useStore } from 'vuex'

export default {
  name: "MyPage",
  props: {
    one: Number,
  },
  setup(props) {
    let followers = ref([]);                // ref: primitive 데이터
    let Originalfollowers = ref([]);

    let test = reactive({name: 'kim'})     // reactive: array, object 데이터
    console.log('test >>', test)

    let { one } = toRefs(props);
    console.log('one.value >>', one.value)

    watch(one, () => {

    })

    let store = useStore();
    console.log('store.state.name >>', store.state.name)

    function searchFollower(searchWord) {
      let resultFollowers = Originalfollowers.value.filter((one) => {
        return one.name.includes(searchWord)
      })
      followers.value = [...resultFollowers]
    }

    onMounted(() => {
      // ajax로 데이터 가져오기
      axios.get('/followers.js')
        .then((response) => {
          followers.value = response.data
          Originalfollowers.value = response.data

          console.log('followers.value >>',  followers.value)
          let followersNum = computed(() => {
            return followers.value.length
          })
          console.log('computed followersNum >>', followersNum.value)
        })
    })

    return {
      followers,
      searchFollower,
    }
  },
}
</script>

<style scoped>
.profile {
  /*background-image: url("https://placeimg.com/100/100/arch");*/
  background-image: url("https://placeimg.com/100/100/arch");
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
</style>