import { createStore } from 'vuex';
import axios from 'axios';

const store = createStore({
  state() {
    return {
      name: '허씨',
      age: 31,
      likes: 120,
      isClickedLike: false,
      more: {}, // 더보기 자료
    }
  },
  mutations: {
    changeName: function(state) {
      state.name = '구씨'
    },
    increaseAge: function(state, payload) {
      state.age += payload
    },
    setLikes: function(state) {
      if (!state.isClickedLike) {
        this.state.likes++
        state.isClickedLike=true
      } else {
        this.state.likes--
        state.isClickedLike=false
      }
    },
    setMore: function(state, payload) {
      state.more = payload
    },
  },
  // ajax 작업, 오래 걸리는 작업 처리
  // 비동기 처리 가능 한 것 같음
  actions: {
    getData: function(context) { // context = $store
      axios.get('https://codingapple1.github.io/vue/more0.json')
        .then((data) => {
          console.log('data.data >>', data.data)
          context.commit('setMore', data.data)
      })
    },
  }
})

export default store;

// actions 실행 순서
// 1. actions를 dispatch('getData')로 실행해서 서버에서 데이터 가져옴
// 2. ajax로 가져온 데이터를
// 3. mutations로 (commit로 실행) state에 저장

// ajax 통신은 actions로
// ajax로 가져온 데이터는 mutations로 state에 저장(가공해서 저장도 가능)